import {css} from 'lit'; export default css`#pwa-install{user-select:none}#pwa-install .install-dialog{--text-color-normal:#333;--background-color:#fff;font-family:Roboto,"Segoe UI",system-ui;position:fixed;display:inline-block;opacity:0;visibility:hidden;z-index:5}#pwa-install .install-dialog.chrome{max-width:380px;width:90%;min-height:90px;margin:0 auto;right:0;left:0;background-color:var(--background-color);filter:drop-shadow(0 5px 15px rgba(0, 0, 0, .3));font-size:15px;color:var(--text-color-normal);border-radius:3px;top:-100px;transition:top .5s ease-in-out 1s,opacity 1.3s ease-in-out .8s,max-height .35s ease-in-out 0s}#pwa-install .install-dialog.chrome .dialog-body{display:grid;grid-template-columns:70px 1fr}#pwa-install .install-dialog.chrome .dialog-body .icon{display:flex;align-items:flex-start}#pwa-install .install-dialog.chrome .dialog-body .icon .icon-image{width:48px;height:48px;margin:0 auto;margin-top:10px;border-radius:3px}#pwa-install .install-dialog.chrome .dialog-body .about{display:grid;grid-template-rows:30px auto}#pwa-install .install-dialog.chrome .dialog-body .about .description,#pwa-install .install-dialog.chrome .dialog-body .about .name{padding:5px 10px;padding-left:5px;overflow:hidden}#pwa-install .install-dialog.chrome .dialog-body .about .name{font-weight:500;padding-top:10px;font-size:18px;line-height:24px;padding:5px 5px 0;align-items:flex-start;display:flex}#pwa-install .install-dialog.chrome .dialog-body .about .description{font-size:14px;font-weight:400;opacity:.7;line-height:20px;padding-bottom:0;padding-right:15px;margin-bottom:3px}#pwa-install .install-dialog.chrome .dialog-body .action-buttons{display:grid;grid-template-columns:50% auto;grid-column-start:1;grid-column-end:3;gap:10px;padding:10px 10px}#pwa-install .install-dialog.chrome .dialog-body .action-buttons .primary{grid-column-start:2}#pwa-install .install-dialog.chrome .dialog-body pwa-gallery{grid-column:1/3;max-height:0;opacity:0;transition:opacity ease-in-out .5s,max-height .5s ease-in-out;overflow:hidden}#pwa-install .install-dialog.chrome.gallery pwa-gallery{transition:opacity .3s ease-in-out .2s,max-height .5s ease-in-out;max-height:70vh;opacity:1}#pwa-install .install-dialog.chrome.available{opacity:1;top:15px;visibility:visible}#pwa-install .install-dialog.mobile{top:auto;bottom:auto;transition:none;transform:translateY(100vh);border-top-left-radius:15px;border-top-right-radius:15px}#pwa-install .install-dialog.mobile .dialog-body{grid-template-columns:1fr}#pwa-install .install-dialog.mobile .dialog-body .touch-header{height:15px;width:100%}#pwa-install .install-dialog.mobile .dialog-body .body-header{display:grid;grid-template-columns:max-content 1fr max-content;grid-gap:10px;padding:15px 15px;padding-top:0}#pwa-install .install-dialog.mobile .dialog-body .body-header .install{text-transform:capitalize}#pwa-install .install-dialog.mobile .dialog-body .body-header .about{grid-template-rows:15px auto;grid-gap:5px}#pwa-install .install-dialog.mobile .dialog-body .body-header .about .name{padding:0;line-height:15px}#pwa-install .install-dialog.mobile .dialog-body .body-header .about .description{padding:0}#pwa-install .install-dialog.mobile .dialog-body .body-header .icon .icon-image{margin:0}#pwa-install .install-dialog.mobile.available{--translateY:translateY(calc(100vh - 100px));opacity:1;top:auto;visibility:visible;transform:var(--translateY)}#pwa-install .material-button{--text-color-primary:#fff;--text-color-secondary:#4285F4;--background-color-primary:#4285F4;--background-color-ripple:#fff;position:relative;display:inline-block;box-sizing:border-box;border:none;border-radius:4px;padding:0 16px;min-width:64px;height:36px;vertical-align:middle;text-align:center;text-overflow:ellipsis;text-transform:uppercase;box-shadow:0 3px 1px -2px rgba(0,0,0,.2),0 2px 2px 0 rgba(0,0,0,.14),0 1px 5px 0 rgba(0,0,0,.12);font-family:Roboto,"Segoe UI",BlinkMacSystemFont,system-ui,-apple-system;font-size:14px;font-weight:500;line-height:37px;overflow:hidden;outline:0;cursor:pointer;transition:box-shadow .2s}#pwa-install .material-button.primary{color:var(--text-color-primary);background-color:var(--background-color-primary)}#pwa-install .material-button.secondary{background-color:transparent;color:var(--text-color-secondary);box-shadow:none}#pwa-install .material-button.secondary::after,#pwa-install .material-button.secondary::before{background-color:var(--background-color-primary)}#pwa-install .material-button.secondary:active,#pwa-install .material-button.secondary:hover{box-shadow:none}#pwa-install .material-button.secondary.close{height:25px;min-width:25px;line-height:0;margin-right:0;margin-left:auto;padding:0;border-radius:50%}#pwa-install .material-button.secondary.close::after{width:25px;height:25px;border-radius:50%}#pwa-install .material-button.secondary.close::before{border-radius:50%}#pwa-install .material-button::before{content:"";position:absolute;top:0;bottom:0;left:0;right:0;background-color:var(--background-color-ripple);opacity:0;transition:opacity .2s}#pwa-install .material-button::after{content:"";position:absolute;left:50%;top:50%;border-radius:50%;padding:50%;width:32px;height:32px;background-color:var(--background-color-ripple);opacity:0;transform:translate(-50%,-50%) scale(1);transition:opacity 1s,transform .5s}#pwa-install .material-button:hover{box-shadow:0 2px 4px -1px rgba(0,0,0,.2),0 4px 5px 0 rgba(0,0,0,.14),0 1px 10px 0 rgba(0,0,0,.12)}#pwa-install .material-button:hover::before{opacity:.08}#pwa-install .material-button:hover:focus::before{opacity:.3}#pwa-install .material-button:active{box-shadow:0 5px 5px -3px rgba(0,0,0,.2),0 8px 10px 1px rgba(0,0,0,.14),0 3px 14px 2px rgba(0,0,0,.12)}#pwa-install .material-button:active::after{opacity:.32;transform:translate(-50%,-50%) scale(0);transition:transform 0s}@media(prefers-color-scheme:dark){#pwa-install .install-dialog.chrome{--text-color-normal:#fff;--background-color:#38373C}#pwa-install .install-dialog.chrome .material-button{--text-color-primary:#1B222C;--text-color-secondary:#89B4F8;--background-color-primary:#89B4F8}}@media(max-width:767px){#pwa-install .install-dialog.chrome.mobile{display:inline-block}#pwa-install .install-dialog.chrome{display:none}}@media(min-width:768px){#pwa-install .install-dialog.chrome.mobile{display:none}#pwa-install .install-dialog.chrome{display:inline-block}}`;
    